.project-page{
    background-color: white;
}
.project-full-image {
    display: flex;
    justify-content: center;
}
.project-full-image img {
    max-height: calc(100vh - 28px - 14vmin);
    max-width: 100%;
}
.project-heading{
    text-align: center;
}
.project-heading h1{
    margin-block-start: 0.2em;
    margin-block-end: 0.2em;
}
